import React, {useState, useEffect} from 'react';
import ArticleList from '../components/ArticleList';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import articuleContent from './articule-content';
import NotFoundPage from './NotFoundPage';
import AddCommentsForm from '../components/AddCommentsForm';

const ArticulePage = ({ match }) => {
    const name = match.params.name;
    const articule = articuleContent.find(articule => articule.name === name);
    const[articleInfo, setArticleInfo] = useState({upvotes : 0, comments : []});

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            console.log(body);
            setArticleInfo(body);
        }
        fetchData();
    }, [name]);
    

    if (!articule) return (
        <NotFoundPage/>
    )
    const otherArticles = articuleContent.filter(articule => articule.name !== name);
    return(
        <>
        <h1>{articule.title}</h1>
        <UpvotesSection articuleName = {name} upvotes = {articleInfo.upvotes} setArticleInfo = {setArticleInfo} />
        {articule.content.map((paragraph, key) => (
            <p key = {key}>{paragraph}</p>
        ))}
        <CommentsList comments = {articleInfo.comments}/>
        <AddCommentsForm articleName = {name} setArticleInfo ={setArticleInfo}/>
        <h3>Other Articles:</h3>
        <ArticleList articles = {otherArticles} />
    </>
    );
    
}
    
export default ArticulePage;